ar sin ser atendido y si no es posible atenderlo por estar con otro cliente u otra razón valedera, decírselo y hacerle saber que tenemos presente que nos está esperando. La indiferencia ante la presencia del cliente ha de ser el peor servicio y a lo sumo en tres minutos causa la irritabilidad del cliente o su retiro de la entidad.")])]),_c('div',{staticClass:"tarjeta color-white-2 p-3",attrs:{"x":"5.5%","y":"50%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Cortesía")]),_c('p',[_vm._v("Es la reina del servicio. La ausencia de la cortesía anula el valor de cualquier otro servicio. Es ahí donde se debe poner en práctica todos los conocimientos de relaciones humanas que se tengan y sacar a brillar la sinceridad y empatía.")])]),_c('div',{staticClass:"tarjeta color-white-2 p-3",attrs:{"x":"14%","y":"80%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Confiabilidad")]),_c('p',[_vm._v("Debe ser el sello de honestidad en el trato comercial. Es la seguridad que los demás depositan para no ser defraudados.")])]),_c('div',{staticClass:"tarjeta color-white-2 p-3",attrs:{"x":"86%","y":"80%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Amistad")]),_c('p',[_vm._v("Es más que un servicio al cliente, es algo que debe trascender al negocio, que debe mantenerse aún fuera del ámbito comercial.")])]),_c('div',{staticClass:"tarjeta color-white-2 p-3",attrs:{"x":"85.8%","y":"17.8%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Ser escuchado")]),_c('p',[_vm._v("Es también una parte del servicio que se le brinda al cliente. Escuchar con atención a las personas cuando hablan, no solo es muestra de respeto, sino que es muestra del interés en ellos. Escuchar lo que nos dicen sus ojos, sus ademanes, su tono de voz o sus movimientos.")])]),_c('div',{staticClass:"tarjeta color-white-2 p-3",attrs:{"x":"94.5%","y":"50%","numero":"+"}},[_c('div',{staticClass:"h5 mb-2"},[_vm._v("Ser comprendido")]),_c('p',[_vm._v("Significa ponerse en la situación de 